import styled from 'styled-components';
import { colors } from '../../utils/colors';
import { device } from '../../utils/layoutBreakpoints';

export const GetDemoBackgroundImagesWrapper = styled.div`
  display: none;
  width: 50%;
  height: 100%;
  @media ${device.laptop} {
    display: block;
  }
`;

export const GetDemoBackgroundImages = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
  margin: 0 auto;
  & > div {
    max-width: 300px;
    height: auto;
    position: absolute;
    &:first-child {
      right: 200px;
    }
    &:nth-child(2) {
      right: -40px;
      top: 250px;
    }
    &:nth-child(3) {
      top: 300px;
      left: -100px;
      z-index: 1;
    }
    &:nth-child(4) {
      right: 80px;
      top: 580px;
    }
  }
`;

export const GetDemoPageWrapper = styled.div`
  width: 100%;
  min-height: 100vh;
  height: 100%;
  background-color: ${colors.secondary.azureishWhite};
`;

export const GetDemoContentContainer = styled.div`
  max-width: 1268px;
  margin: auto;
  padding: 30px 40px 80px;
  display: flex;
  height: 100%;
  & > div:first-child {
    width: 100%;
    @media ${device.laptop} {
      width: auto;
    }
  }
`;

export const GetDemoPageHeader = styled.div`
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 100%;
  margin: 0 auto;

  @media ${device.laptop} {
    margin-top: 20px;
  }
`;
