import { StaticImage } from 'gatsby-plugin-image';
import {
  GetDemoBackgroundImagesWrapper,
  GetDemoBackgroundImages,
} from './styled';
import React from 'react';

export const BackgroundImages = () => {
  return (
    <GetDemoBackgroundImagesWrapper>
      <GetDemoBackgroundImages>
        <div>
          <StaticImage
            src="../../images/GetDemo/automatedNumberPorting.png"
            placeholder="none"
            alt=""
          ></StaticImage>
        </div>
        <div>
          <StaticImage
            src="../../images/GetDemo/messageTimeRouting.png"
            placeholder="none"
            alt=""
          ></StaticImage>
        </div>
        <div>
          <StaticImage
            src="../../images/GetDemo/phoneNumberVerification.png"
            placeholder="none"
            alt=""
          ></StaticImage>
        </div>
        <div>
          <StaticImage
            src="../../images/GetDemo/advancedErrorReporting.png"
            placeholder="none"
            alt=""
          ></StaticImage>
        </div>
      </GetDemoBackgroundImages>
    </GetDemoBackgroundImagesWrapper>
  );
};
